<template>
  <div class="register">
    <mt-field label="" placeholder="" v-model="username"></mt-field>
    <mt-field label="" placeholder="" type="number" v-model="phone"></mt-field>
    <mt-field label="QQ" placeholder="QQ" type="number" v-model="QQ"></mt-field>
    <mt-field label="" placeholder="" type="password" v-model="password"></mt-field>
    <mt-button type="danger" @click="sub"></mt-button>
  </div>
</template>

<script>
  import { MessageBox } from 'mint-ui';
  export default {
    data(){
      return {
        username:'',
        phone:'',
        QQ:'',
        password:''
      }
    },
    methods:{
      sub(){
       if(this.username && this.phone && this.QQ && this.password){
         this.$store.state.aa.people[this.name] =this.phone;
         this.$store.state.aa.people[this.psw] =this.password;
         MessageBox({
           title: '',
           message: '',
           confirmButtonText:''
         }).then(action => {
          this.$router.push('/login');
         });
       }else{
         MessageBox({
           title: '',
           message: '',
           confirmButtonText:''
         });
       }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.mint-cell-wrapper{
  text-align: left;
}
</style>



